import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core'
import { Http } from '@angular/http';
import { Observable } from 'rxjs';
import { GlobalState } from '../../../global.state';
import 'rxjs/add/operator/catch'; // Import operators as needed
import 'rxjs/add/observable/throw';
@Injectable()
export class UserToken {
  public accessToken: string | null = null;
  public refreshToken: string | null = null;
  private tokenExpirationTime: Date | null = null;
  private apiUrl: string;

  constructor(private http: Http, private gblstate: GlobalState,) {
    let arr = this.gblstate.getGlobalSetting("apiUrl");
    if (arr) { this.apiUrl = arr[0] }
  }

  // Set access token and refresh token with expiration time
  setTokens(accessToken: string, refreshToken: string, expiresIn: number) {
    this.accessToken = accessToken;
    this.refreshToken = refreshToken;
    this.tokenExpirationTime = new Date(Date.now() + expiresIn);
  }

  // Check if access token is expired
  isAccessTokenExpired(): boolean {
    return this.tokenExpirationTime !== null && this.tokenExpirationTime <= new Date();
  }


  getAccessToken(): string {
    return this.accessToken;
  }

  // Check if user is authenticated (based on access token and expiration)
  isAuthenticated(): boolean {
    return !!this.accessToken && !this.isAccessTokenExpired();
  }

  // Clear tokens (logout)
  clearTokens() {
    this.accessToken = null;
    this.refreshToken = null;
    this.tokenExpirationTime = null;
  }
}


