import { Injectable, Injector } from "@angular/core";
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor
} from "@angular/common/http";
import { Observable } from "rxjs/Observable";
import { UserToken } from "../permission";
import { LoginService } from "../../../pages/login";

@Injectable()
export class TokenInterceptor implements HttpInterceptor {
  constructor(private injector: Injector,private tokenService:UserToken,private loginService:LoginService) { }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    if (this.tokenService.isAuthenticated() && this.tokenService.isAccessTokenExpired()) {
      // Access token is expired, attempt to refresh it
      this.tokenService.refreshAccessToken().
      flatMap((refreshed: any) => {
          if (refreshed) {
            // Retry the original request with the new access token
            request = request.clone({
              setHeaders: {
                Authorization: `Bearer ${this.tokenService.getAccessToken()}`
              }
            });
            return next.handle(request);
          } else {
            // Refresh token failed, redirect to login or handle accordingly
            this.tokenService.clearTokens();
            this.loginService.logout();
            // You may handle logout or redirect to login page here
          }
        });
       
      
    } else {
      // Access token is valid, proceed with the original request
      request = request.clone({
        setHeaders: {
          Authorization: `Bearer ${this.tokenService.getAccessToken()}`
        }
      });
      return next.handle(request);
    }
  }

  // intercept(
  //   request: HttpRequest<any>,
  //   next: HttpHandler
  // ): Observable<HttpEvent<any>> {
  //   request = request.clone({
  //     setHeaders: {
  //       Authorization: localStorage.getItem("TOKEN")
  //     }
  //   });
  //   return next.handle(request);
  // }
}



